  position: absolute;
  width: 34px;
  height: 34px;
  top: 8px;
  left: 8px;
  background: rosybrown;
  border-radius: 99999px;
}

#shadow {
  position: absolute;
  top: -38px;
  left: 5px;
  width: 34px;
  height: 34px;
  background: rosybrown;
  border-radius: 99999px;
}

