.modal {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(34, 34, 34, .6);
  z-index: 7;
}

.modal-content {
  margin: 0 auto;
  z-index: 8;
}

/* Mobile Styles */
@media (max-width: 700px) {
  .hide-sm, .mobile-d-n {
    display: none;
  }

  .grid-2,
  .grid-3,
  .grid-4 {
    grid-template-columns: 1fr;
  }

  /* Text Styles */
  .x-large {
    font-size: 3rem;
  }

  .large {
    font-size: 2rem;
  }

  .lead {
    font-size: 1rem;
  }

  /* Navbar */
  .navbar {
    display: block;
    text-align: center;
  }

  .navbar h1 {
    margin-bottom: 0.6rem;
  }

  .navbar ul {
    text-align: center;
    justify-content: center;
  }
}

::-webkit-scrollbar {
  background: rgba(110, 110, 110, 0.7);
  border-radius: 0.25rem;
  width: 10px;
